import React, { useContext, useState, useEffect } from 'react';
import Box from '@mui/material/Box';
import Badge from '@mui/material/Badge';
import Button from '@mui/material/Button';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Tooltip from '@mui/material/Tooltip';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import Stack from '@mui/material/Stack';

//  Componentes
import ProductCart from './ProductCart';

// Utilidades
import { formatNumber } from '../local/ProductModal';

//Contextos
import { UserDataContext } from '../../../context/UserDataContext';
import { ContextTemplate } from '../ContextTemplate';

export default function ShoppingCart() {
    const { openNotification } = useContext(ContextTemplate); // contexto del tema general y notificacion
    const { hoyosData } = useContext(UserDataContext); // Contexto de los datos de usuario
    const { token, typeDelivery, typeUser} = hoyosData; // Desestructuro los datos de hoyostools

    const [anchorEl, setAnchorEl] = useState(null);
    const [numberProducts, setNumberProducts] = useState(0);
    const [productsList, setProductsList] = useState([]);
    const [totalPrice, setTotalPrice] = useState(0);

    const open = Boolean(anchorEl);
    const handleClick = (event) => {
        getDataBasket();
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    //  Función que actualiza los datos del carrito cada vez que se agrega o elimina producto
    const getDataBasket = () => {

        // const url = process.env.ENDPOINT_GET_DATA_BASKET + typeDelivery;
        // fetch(url, {
        //     method: 'GET',
        //     headers: {
        //     'Authorization': 'Bearer ' + token
        //     }
        // })
        // .then(res => res.json())
        // .then(response => {
        //     //console.log('Info Canasta:', response)
        //     //if error -- sesion expirada

        //     if(response.error == false){
        //         // console.log(response)
        //         setNumberProducts(response.cartQuantity);
        //         setProductsList(response.itemsCanastaUsuario);
        //         setTotalPrice(formatNumber(response.cartTotal, 0));

        //     }else{
        //         openNotification(response.message, 'error');
        //     }

        // })
        // .catch(error => console.error('Error:', error));
    }


    const goToPayment = ()=> {
        var url ='';
    
        // if(typeUser == "1"){
        //     url = "https://ferreteriaonlinehoyostools.com/#!/app/return-vr/" + token + "/" + typeDelivery;
        // }else{
        //     url = "https://ferreteriaonlinehoyostools.com/#!/app/return-vr-b2c/"  + token;
        // } 
        // window.open(url, "_blank");
    }
    const onRemoveProductCart = (product) => {
        
        var filtered = productsList.filter(function(value, index, arr){ 
            return value !== productsList;
        });  
        setProductsList(filtered);
        setNumberProducts(numberProducts - 1);

        // const productToDelete = productsList.find(p => p == product)
        // if(productToDelete){
            
        // }
    }

    const addProductToCart = (e) =>{
        
        const product_data = e.detail.dataProduct
        
        const new_array_products = [...productsList, product_data]
        console.log('agregando', new_array_products)
        setProductsList(new_array_products);
        setNumberProducts(numberProducts+1);
        setTotalPrice(formatNumber(10000, 0));
        setNumberProducts(new_array_products.length);
    }

    useEffect(() => {
        getDataBasket();

        document.addEventListener('spawn_product_event', addProductToCart, false);
        return () => document.removeEventListener("spawn_product_event", addProductToCart);
    }, []);

    return (
        <React.Fragment>
            {/* <Box sx={{ display: 'flex', alignItems: 'center', textAlign: 'center' }}
                className='interactable-ui'> */}

            <Tooltip title="Mi carrito de compras" >
                <IconButton
                    onClick={handleClick}
                    size="small"
                    sx={{ ml: 2, color:'white' }}
                    aria-controls={open ? 'shopping-cart-menu' : undefined}
                    aria-haspopup="true"
                    aria-expanded={open ? 'true' : undefined}
                >
                    <Badge color="secondary" badgeContent={numberProducts}>
                        <ShoppingCartIcon />
                    </Badge>

                </IconButton>
            </Tooltip>
            {/* </Box> */}
            <Menu
                anchorEl={anchorEl}
                id="shopping-cart-menu"
                open={open}
                onClose={handleClose}
                // onClick={handleClose}
                PaperProps={{
                    elevation: 0,
                    sx: {
                        overflow: 'visible',
                        filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
                        mt: 1.5,
                        '& .MuiAvatar-root': {
                            width: 32,
                            height: 32,
                            ml: -0.5,
                            mr: 1,
                        },
                        '&:before': {
                            content: '""',
                            display: 'block',
                            position: 'absolute',
                            top: 0,
                            right: 14,
                            width: 10,
                            height: 10,
                            bgcolor: 'background.paper',
                            transform: 'translateY(-50%) rotate(45deg)',
                            zIndex: 0,
                        },
                    },
                }}
                transformOrigin={{ horizontal: 'right', vertical: 'top' }}
                anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
            >
                <Box sx={{ width: '100%', maxHeight: '50vh', overflow: 'hidden', overflowY: "scroll" }}>
                    {numberProducts > 0 ?
                        productsList.map((product, i) =>
                            // <ObjectRow key={i} />
                            <MenuItem key={i}>
                                <ProductCart 
                                    name={product.product_name} 
                                    itemCode={product.product_code}
                                    itemId={product.product_code}
                                    image={process.env.URL_AMAZON_FILES + product.product_image} 
                                    quantity={1} 
                                    price={1000}
                                    onRemoveProductCart={()=>onRemoveProductCart(product)} />
                            </MenuItem>
                        )
                        :
                        <MenuItem>
                            <Typography sx={{ width: '100%', color: 'gray' }} component="div" variant="Button">
                                no has agregado productos al carrito
                            </Typography>
                        </MenuItem>

                    }
                </Box>

                <Divider />

                {   numberProducts > 0 &&
                    <MenuItem disabled>
                        <Stack direction="row" justifyContent="center" spacing={2} sx={{ width:'100%', p:1 , textAlign:'center' }}>
                            <Typography sx={{ fontWeight: 'bold' }} color="textPrimary">
                                TOTAL A PAGAR:
                            </Typography>
                            <Typography color="textPrimary" >
                                {totalPrice}
                            </Typography>
                        </Stack>
                    </MenuItem>
                }
                <MenuItem>
                    <Button fullWidth onClick={goToPayment}>IR AL PAGO</Button>
                </MenuItem>

            </Menu>
        </React.Fragment>
    );
}