/** FORMATO A NÚMEROS PRECIOS *********************/
export function formatNumber(valor, decimal) {
  var formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',

    // These options are needed to round to whole numbers if that's what you want.
    //minimumFractionDigits: 0, // (this suffices for whole numbers, but will print 2500.10 as $2,500.1)
    maximumFractionDigits: decimal, // (0 causes 2500.99 to be printed as $2,501)
  });

  return (formatter.format(valor));
}

const Img = styled('img')({
  margin: 'auto',
  display: 'block',
  maxWidth: '100%',
  maxHeight: '100%',
});

const ProductField = ({label, value, loading}) => {
  return(
    <Stack direction="row" justifyContent="space-between" spacing={2} sx={{ width:'75%', mb:{xs: 1, md:0}, textAlign:'left' }}>
      <Typography sx={{ width:'60%', fontWeight: 'bold', lineHeight:'110%' }} color="textPrimary">
        {label}
      </Typography>
      <Typography color="textSecondary" >
        {loading ? <Skeleton width='10ch' /> : value}
      </Typography>
    </Stack>
  )
}


const PriceLabel = ({label, value, loading, ivaLabel = false, lineTrough = false}) => {
  return(
    <Stack spacing={1} direction="row" alignItems='flex-start' justifyContent='center' sx={{ width:'85%', textAlign: 'left'}}>
      <Typography color="textSecondary" sx={{  width: '40%', fontSize: { xs: '1rem ', md: '1.3rem' }, fontWeight: 'bold' }}>{label}:</Typography>
      <Stack sx={{width:'60%'}} spacing={0} direction="column" justifyContent={'flex-end'}>
        <Stack spacing={1} direction="row" justifyContent={'flex-end'} sx={{ textAlign:'left' }}>
          <Typography 
            variant="textPrimary" 
            sx={{ 
              fontSize:{ xs: '1rem ', md: '1.3rem' }, 
              textDecoration: lineTrough? "line-through" : 'none', 
            }}
          >
              {value}
          </Typography>
          {/* <ListItemText primary="COP" secondary={ivaLabel? "iva incluido":null} /> */}
          <Typography color="textSecondary" sx={{ fontWeight: 'light' }}>COP </Typography>
        </Stack>
        {ivaLabel &&
          <Typography color="textSecondary" 
            sx={{ textAlign:'right', fontWeight: 'light', fontSize:'0.8rem', marginTop:'-0.3rem' }}
          >
            Iva incluido 
          </Typography>}
      </Stack>
    </Stack>
  )
}
