//  Al cargar la escena manejo los eventos de click del mouse sobre el canvas de la escena
//  Esto es útil para detectar eventos sobre el los elementos three que no estan montados en aframe
AFRAME.registerComponent('scene-loader', {
    init: function () { 

        let raycaster = new THREE.Raycaster();
        raycaster.far =  20 ;

        let mouse = new THREE.Vector2();

        const cameraThree = document.querySelector("[camera]").getObject3D('camera');
        const rendererThree = this.el.renderer.domElement;
        const canvasThree = this.el.canvas;
        const sceneThree = this.el.object3D; 

        canvasThree.addEventListener('click', onDocumentMouseDown, false);
        canvasThree.addEventListener('touchend', onDocumentMouseDown, false);
        
        function onDocumentMouseDown( event ) {
            // console.log('event', event)
            // event.preventDefault();
            // console.log('click', document.body.clientWidth);
            const coorX = event.clientX || event.changedTouches[0].clientX;
            const coorY = event.clientY || event.changedTouches[0].clientY;
            mouse.x = ( coorX / window.innerWidth ) * 2 - 1; // document.body.clientWidth 
            mouse.y = - ( coorY /  window.innerHeight) * 2 + 1; //document.body.clientHeight

            //console.log('event', document);

            raycaster.setFromCamera( mouse, cameraThree );
            // console.log(scene.children);
            let intersects = raycaster.intersectObjects( sceneThree.children );
            // console.log('interseccion',intersects[0]);

            if ( intersects.length > 0 ) {
                if (intersects[0].object && intersects[0].object.callback instanceof Function) {
                    intersects[0].object.callback();
                } 
                
            }       
        }
    },

    
});

