AFRAME.registerComponent("load-shelves", {
    schema: {
      shelvesDataArray: {type: 'string', default: '[]'},
      awsParameters: {type: 'string', default: '{}'},
      scale: {type: 'int', default: 50}
    },

    init: function () {

        this.matrix = new THREE.Matrix4();
        this.debugMatrix = new THREE.Matrix4();
        this.componentMatrix = new THREE.Matrix4();

        this.position = new THREE.Vector3();
        this.quaternion = new THREE.Quaternion();
        this.scale = new THREE.Vector3();


        this.instancedMeshes = [];
        this.componentMatrices = [];

        this.functionsQueue = [];
        this.activeFunctionsQueue = false;

        let shelvesDataArray = JSON.parse(this.data.shelvesDataArray);
        let gondolas_aws_parameters = JSON.parse(this.data.awsParameters);


        // console.log('data', JSON.parse(shelvesDataArray[0].ubicacion_gondola_tipo_1))
        
        //  Obtengo el tamaño del arreglo de estantes para determinar el tamaño en InstnacedMesh
        this.instancesCount = shelvesDataArray.length;

        //  Obtengo un valor de escala para poder escalar posiciones de las instancias
        this.scaleFactor = this.el.object3D.scale.x;

        this.el.addEventListener('model-loaded', e => {

            let originalMesh = this.el.getObject3D('mesh');
            this.meshNodes = this.constructMeshNodes(originalMesh);

            this.meshNodes.forEach((node) => {
                var instancedMesh = new THREE.InstancedMesh(node.geometry,
                                                            node.material,
                                                            this.instancesCount);
                    
                // For each instanced mesh required, we store off both the instanced mesh
                // itself. and the transform matrix for the component of the model that
                // it represents.
                this.instancedMeshes.push({instancedMesh:instancedMesh, originalColor: node.originalColor});
                this.componentMatrices.push(node.matrixWorld);

            });

            this.instancedMeshes.forEach(mesh => {
                this.el.object3D.add(mesh.instancedMesh);
            });


            // obtengo las dimensiones del modelo para agregar la caja de colición después.
            const sizeModel = new THREE.Vector3();
            let box = new THREE.Box3().setFromObject(originalMesh.clone());
            box.getSize(sizeModel);
            //  Temporalmente - se multiplica por la escala que se le quiera dar al objeto
            sizeModel.multiplyScalar(this.data.scale);

            // Remuevo la maya original para reemplazarla por una de las instancias nuevas
            this.el.object3D.remove(originalMesh);

            // set the Object3D Map to point to the first instanced mesh.
            this.el.setObject3D('mesh', this.instancedMeshes[0].instancedMesh);   
            
            


            // Creo una instancia para cada estante de la lista a partir del modelo original
            shelvesDataArray.forEach(async(shelfData, index) => {

                
                const positionDataString = shelfData[gondolas_aws_parameters.ubicacion_gondola_label].replace(/'/g, '"')
                const dataPosRot = JSON.parse(positionDataString)
                const element = document.createElement('a-entity');
                element.object3D.rotation.set(
                    THREE.Math.degToRad(dataPosRot.rotation.x),
                    THREE.Math.degToRad(dataPosRot.rotation.y),
                    THREE.Math.degToRad(dataPosRot.rotation.z)
                );
                element.object3D.position.set(
                    dataPosRot.position.x / this.scaleFactor,
                    dataPosRot.position.y / this.scaleFactor,
                    dataPosRot.position.z / this.scaleFactor
                );

                //  Temporalmente - aplico la escala directamente a la instancia y no a la entidad html
                element.object3D.scale.set(this.data.scale , this.data.scale, this.data.scale);
                
                const get_Color_Cuerpo =  shelfData[gondolas_aws_parameters.color_cuerpo_gondola_label];
                const shelfColor  = get_Color_Cuerpo? new THREE.Color().set(get_Color_Cuerpo) : null;

                const get_Color_border = shelfData[gondolas_aws_parameters.color_borde_gondola_label]
                const borderColor  = get_Color_border? new THREE.Color().set(get_Color_border): null;
                // borderColor.set(get_Color_border);

                this.addInstance(element, shelfColor, borderColor, index);

                //  Crear logos
                gondolas_aws_parameters.logos.forEach(logo =>{

                    this.createLogo(element, {
                        image: shelfData[logo.imageLabel],//logo.image,
                        position: logo.position,
                        rotation: logo.rotation,
                        width: logo.with,
                        height: logo.height,
                    });
                })
                
                
                //  Creo la caja de colisión para cada instancia según su tamaño
                element.setAttribute('ammo-body', {type: 'static', scaleAutoUpdate:false});
                element.setAttribute('ammo-shape', {
                    type: 'box', 
                    fit:'manual', 
                    halfExtents:`${sizeModel.x * this.scaleFactor/2} ${sizeModel.y * this.scaleFactor/2} ${sizeModel.z*this.scaleFactor/2}`,
                    offset: `0 ${sizeModel.y * this.scaleFactor/2} 0`
                });

                //  Crear esfera para activar la visualización de productos
                const spehereCol = document.createElement('a-sphere');
                spehereCol.setAttribute('visible',"false");
                spehereCol.setAttribute('ammo-body',{
                    type: 'static', 
                    emitCollisionEvents:true,
                    disableCollision:true
                });
                spehereCol.setAttribute("ammo-shape", "type:sphere");
                spehereCol.setAttribute("radius", "8");

                //  Espero a que el jugador entre en el rango cercano a la góndola para activar los productos
                //  Esto es útil para que no se sobrecargue de operaciones el inicio de la experiencia
                spehereCol.addEventListener('collidestart', async(e)=>{
                    if(e.detail.targetEl.id ==='player-entity'){
                        //loadProductsShelf ();
                        //Consulto los productos y los agrego a la lista de tareas para graficarlo
                        try{
                            const productList = await this.getProductsGondola( 
                                shelfData[gondolas_aws_parameters.id_gondola_label],
                                gondolas_aws_parameters.nombre_gondola_productos, gondolas_aws_parameters.token);

                            //  Verifico que la consulta se haya hecho correctamente y que el arreglo tenga elementos para agregar a productos
                            if(productList && productList.length > 0){
                                //  Grafico los productos existentes y consultados de esta gondola
                                this.createProducts(element, productList);
                            }
                        }catch(error){
                            console.error('error al consultar productos', error)
                        }
                        //  Elimino el elemento esfera por que ya no se necesita una vez han cargado los productos
                        spehereCol.parentNode.removeChild(spehereCol);
                    }
                });

                
                


                element.appendChild(spehereCol);
                this.el.appendChild(element);
                // this.el.object3D.scale.set(1, 1, 1);
              
            });

      })

    },

    constructMeshNodes: function(originalMesh) {
        let meshNodes  = [];
        originalMesh.updateMatrixWorld();

        originalMesh.traverse(node => {
            if(node.type == "Mesh"){//if (node.geometry && node.material) {
                let geometry = node.geometry;
                let material = node.material;
                
                //  Se agrega esto por que el material del estante por alguna razón es muy oscuro
                //  Con esto el color se cambia por otro
                let mat = new THREE.MeshStandardMaterial({
                    name: material.name,
                    color: new THREE.Color("rgb(255, 255, 255)"),// '0xffffff', 
                    map: material.map
                })

               
                //  Error partes invisibles puede ser aquí 
                //  Solucionado temporalmente dejando la escala de la entidad original en 1
                //  y se escalan las instancias
                node.updateMatrixWorld();
                let matrix_aux = node.matrixWorld.clone();
                matrix_aux.premultiply(originalMesh.matrixWorld.invert());

                //  Por medio de esta comprobación se puede imprimir el valor de dimensiones
                //  de la instancia, el error ocurre después de 2 iteraciones al parecer
                // if(material.name == 'Logo_Color'){
                //     // matrix_aux.multiplyScalar(1/2500);
                //     // console.log(node.material.name, node);
                //     let pos = new THREE.Vector3();
                //     let rot = new THREE.Quaternion();
                //     let sca = new THREE.Vector3();
                //     // this.componentMatrix.makeScale(1,1,1);
                //     let matrix_aux_2 = node.matrixWorld.premultiply(originalMesh.matrixWorld.invert());
                //     matrix_aux.decompose(pos, rot, sca);
                //     console.log('pos', pos)
                //     console.log('rot', rot)
                //     console.log('sca', sca)
                // }
                    
                
                meshNodes.push({'geometry' : geometry,
                                'material' : mat, //material.name === 'Estantes_Color'? mat : material,
                                'matrixWorld': matrix_aux,
                                'originalColor': material.color});
              
            }
          
        })

        return meshNodes;
    },

    matrixFromMemberObject(object3D) {

        const matrix = this.matrix;
        matrix.compose(object3D.position,
            object3D.quaternion,
            object3D.scale);

        return matrix;
    },

    addInstance: function(member, shelfColor , bordeColor , index) {

        this.matrix = this.matrixFromMemberObject(member.object3D);

        this.instancedMeshes.forEach((instancedMesh, componentIndex) => {

            const mesh = instancedMesh.instancedMesh
            if(mesh.material.name === 'Estantes_Color'){
                mesh.setColorAt(index, shelfColor ? shelfColor : instancedMesh.originalColor);
            }

            if((mesh.material.name === 'Borde_Estantes' || //tipo 1
                mesh.material.name === 'Borde_Estante_Trans' || //tipo 2
                mesh.material.name === 'Borde_Estante' ||mesh.material.name === 'Borde_3')// tipo 3
                
            ) {
                mesh.setColorAt(index, bordeColor?bordeColor : instancedMesh.originalColor);
            }

            // console.log('matrix', this.componentMatrices[componentIndex])
            this.componentMatrix = this.matrix.clone();
            
            // Error aquí que oculta las partes de las góndolas
            this.componentMatrix.multiply(this.componentMatrices[componentIndex]); 

            mesh.setMatrixAt(index, this.componentMatrix);

            mesh.instanceMatrix.needsUpdate = true;
        });

        this.instancedMeshes.forEach(mesh => {
            mesh.instancedMesh.count = this.instancesCount;
            mesh.instancedMesh.instanceMatrix.needsUpdate = true;
        });
        

    }, 

    createLogo: function(parentElement, dataLogo){
        //  Ancho y alto del plano que representa el producto

        const widthPlane = dataLogo.width ;
        const heightPlane = dataLogo.height ;

        //  Revisar después esta validación para graficar sólo imágenes válidas
        if(!dataLogo.image || !dataLogo.image.includes('archivos/')) return;

        let urlImage = process.env.URL_AMAZON_FILES + dataLogo.image; 

        let texture = new THREE.TextureLoader().load(urlImage,
            //  Bind me permite acceder al this de las funciones del componente aframe y ejecutar this.fitImageToPlane
            (function ( texture ) {
                // this.fitImageToPlane(texture, widthPlane, heightPlane);
            }).bind(this),
            

            // onProgress callback currently not supported
            undefined,

            // onError callback
            function ( err ) {
                console.log( 'An error happened.', err );
            }
        );
        
        

        const geometry = new THREE.PlaneGeometry( widthPlane, heightPlane, 1 ,1 );
        const material = new THREE.MeshBasicMaterial( {
            color: new THREE.Color( 0xffffff ), 
            map: texture,
            side: THREE.FrontSide
        } );

        const plane = new THREE.Mesh( geometry, material );
        // plane.scale.set(1/this.data.scale, 1/this.data.scale, 1/this.data.scale);

        plane.position.set(
            dataLogo.position.x / this.data.scale, 
            dataLogo.position.y / this.data.scale, 
            dataLogo.position.z / this.data.scale,
        );

        //  Por alguna razón se deben invertir los ejes X y Z en three js product.rotation.z
        plane.rotation.set(
            THREE.Math.degToRad(dataLogo.rotation.x),
            THREE.Math.degToRad(dataLogo.rotation.y),
            THREE.Math.degToRad(dataLogo.rotation.z),
            "ZYX"
        );

        plane.scale.set(plane.scale.x/this.data.scale,
            plane.scale.y/this.data.scale,
            plane.scale.z/this.data.scale
        );
        
        plane.updateMatrix();
        // plane.callback = function() { alert('logo');}

        this.addToQueue( async function(){await parentElement.object3D.add( plane );});
        
    },

    // Consulto los productos de la base de datos
    getProductsGondola: async function(id_gondola, tipoGondolaName, token) {

        let productList = null;

        const body = {
            "request_data": {
                "type_action": "get_all_by_g",
                "data": {
                    "id_empresa_usuario_producto": process.env.EVENTVR_ID_EMPRESA_USUARIO,
                    "id_gondola": id_gondola,
                    "nombre_campo_id": tipoGondolaName
                }
            },
            "headers": {
                "Content-Type": "application/json",
                "Accept": "application/json",
                "Authorization": token
            }
        }

        try {
            const response = await axios.post(url + 'productos', body, {
                headers: {
                    "Content-Type": "application/json",
                    "Accept": "application/json"
                },
            })

            if(response.status == '200' && response.data.status != 'failed'){
                //obtengo el arreglo de productos
                productList = response.data?.body?.data || []

            }

        } catch (err) {
            console.error(err);
        }

        return productList;

    },

    createProducts: function(parentElement, productsData){
        
        productsData.forEach((product, i) => {

            if(product.imagen_producto && product.imagen_producto !=='imagen_producto'){
                console.log('producto' , product)
            }

            //Valido que exista imágen y código para graficar el producto
            if(!product.imagen_producto || product.imagen_producto =='imagen_producto') return;
            

            // Agrego el codigo de producto y la ubicación de su gondola a la lista de busqueda
            productsInstances.push({
                code: product.codigo_producto, 
                parentPosition: parentElement.object3D.position
            })

            //  Si la ubicación espacial no existe no se debería graficar
            if(!product.posicion_producto || product.posicion_producto =='') return;

            // Valido que existan los datos necesarios para renderizar el producto
            const ubicacionProductoString = product.posicion_producto.replace(/'/g, '"')
            const ubicacionProducto = JSON.parse(ubicacionProductoString);
            if(!ubicacionProducto.width &&
                !ubicacionProducto.height &&
                !ubicacionProducto.position &&
                !ubicacionProducto.rotation) return;

            //  Ancho y alto del plano que representa el producto
            const widthPlane = ubicacionProducto.width ;
            const heightPlane = ubicacionProducto.height ;

            let urlImage = process.env.URL_AMAZON_FILES + product.imagen_producto; 

            let texture = new THREE.TextureLoader().load(urlImage,
                //  Bind me permite acceder al this de las funciones del componente aframe y ejecutar this.fitImageToPlane
                (function ( texture ) {
                    this.fitImageToPlane(texture, widthPlane, heightPlane);
                }).bind(this),
            );
            
            

            const geometry = new THREE.PlaneGeometry( widthPlane, heightPlane );
            const material = new THREE.MeshBasicMaterial( {
                color: new THREE.Color( 0xffffff ), 
                map: texture,
                side: THREE.FrontSide
            } );
            const plane = new THREE.Mesh( geometry, material );

            plane.position.set(
                ubicacionProducto.position.x / this.data.scale, 
                ubicacionProducto.position.y / this.data.scale, 
                ubicacionProducto.position.z / this.data.scale,
            );

            //  Por alguna razón se deben invertir los ejes X y Z en three js product.rotation.z
            plane.rotation.set(
                THREE.Math.degToRad(ubicacionProducto.rotation.x),
                THREE.Math.degToRad(ubicacionProducto.rotation.y),
                THREE.Math.degToRad(ubicacionProducto.rotation.z),
                "ZYX"
            );

            plane.scale.set(plane.scale.x/this.data.scale,
                plane.scale.y/this.data.scale,
                plane.scale.z/this.data.scale
            );

            plane.updateMatrix();

            //  Callback para ejecutar cuando se clickee sobre el elemento 3D
            plane.callback = (function() { this.onProductClick(
                product.codigo_producto,
                product.nombre_producto,
                product.descripcion_producto,
                product.imagen_producto
            );}).bind(this)

            this.addToQueue( async function(){await parentElement.object3D.add( plane );})
            

        });
        
    },
    
    onProductClick: function(productCode = '', name='', description = '', image, ){
        const ClickProductEvent = new CustomEvent('open_product_modal', { 
            detail:{
              product_code: productCode, //'2322' con descuento //'3528' con descri // 6130 sin stock
              product_name:  name,
              product_description: description,
              product_image: image
            } 
        });
        document.dispatchEvent(ClickProductEvent);
    },

    //  Con esta función se ajusta el tamaño de las texturas three js para que conicidad 
    //  con el tamaño de un plano y no pierdan su aspect ratio
    fitImageToPlane: function(texture, widthPlane, heightPlane){
        let imgRatio = texture.image.width / texture.image.height;
        let planeRatio = (widthPlane) / (heightPlane);
        let aspect = imgRatio / planeRatio;

        texture.wrapS = THREE.ClampToEdgeWrapping;
        texture.wrapT = THREE.ClampToEdgeWrapping;

        // si la imágen es más ancha que alta
        if(imgRatio > 1){
            // console.log('ancha')
            texture.repeat.set(1, texture.image.width / texture.image.height);
            texture.offset.y = (texture.repeat.y - 1) / 2 * -1;
            
        }else{
            // console.log('larga')
            texture.repeat.set(texture.image.height / texture.image.width, 1);
            texture.offset.x = (texture.repeat.x - 1) / 2 * -1;
        }

    },

    addToQueue: function(e){
        this.functionsQueue.push(e);
        if(!this.activeFunctionsQueue){
            this.processQueue();
        }
    },

    processQueue: async function(){
        this.activeFunctionsQueue = true;
        // console.log('ejecutando cola');
        if(this.functionsQueue.length > 0){
            const func = this.functionsQueue[0];
            // console.log('ejecutando funcion');
            await func();
            //  Remuevo la primera función - la que acabo de ejecutar en la cola
            this.functionsQueue.shift();
            await new Promise(resolve => setTimeout(resolve, 100));
            this.processQueue()
        }else{
            this.activeFunctionsQueue = false;
        }
    }
})