import React, { useState, useRef } from 'react';
import { styled, alpha } from '@mui/material/styles';
import InputBase from '@mui/material/InputBase';
import SearchIcon from '@mui/icons-material/Search';
import IconButton from '@mui/material/IconButton';
import Menu from '@mui/material/Menu';
import Box from '@mui/material/Box';
import MenuItem from '@mui/material/MenuItem';
import ListItemText from '@mui/material/ListItemText';
import Tooltip from '@mui/material/Tooltip';
import ListItem from '@mui/material/ListItem';
import Typography from '@mui/material/Typography';
import { Divider } from '@mui/material';


const SearchedItem = ({ productName="Producto", productCode }) => {
  
  const OnClickProduct = () => {
    const ClickProductEvent = new CustomEvent('open_product_modal', { 
      detail:{
        product_code: productCode 
      } 
    });
    document.dispatchEvent(ClickProductEvent);

    const SearchProductEvent = new CustomEvent('search_product_position', { 
      detail:{
        product_code: productCode 
      } 
    });
    document.dispatchEvent(SearchProductEvent);

    
  }
  
  return(

    <ListItem onClick={OnClickProduct}>
      {/* <ListItemText noWrap>{productName}</ListItemText> */}
      <Typography noWrap variant="body2" color="text.secondary">
        {productName}
      </Typography>
    </ListItem>
    
  )
}

const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: '100%',
  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing(1),
    width: 'auto',
  },
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: '12ch',
      '&:focus': {
        width: '20ch',
      },
    },
  },
}));



export default function SearchInput() {

  const [foundProducts, setFoundProducts] = useState([]);
  
  const [anchorEl, setAnchorEl] = useState(null);

  const searcInputref = useRef(null);

  const open = Boolean(anchorEl);

  const handleOpenSearchMenu = (event) => {
    setAnchorEl(event.currentTarget);
    // event.currentTarget.focus()
    setFoundProducts([]);
  };

  const handleCloseSearchMenu = () => {
    setAnchorEl(null);
  };



  const onIputSearchChange = (e) =>{
    const url = `${process.env.ENDPOINT_SEARCH_ITEMS_HOYOS}?cadena=${e.target.value}&limit=20`
  
    fetch(url, {
      method: 'GET',
    })
    .then(res => res.json())
    .then(response => {
      // console.log('Productos que coinciden: ', response)
      setFoundProducts(response);
    })
    .catch(error => console.error('Error:', error));
  }

  return (
    <>
      {/* <Search>
        <SearchIconWrapper>
          <SearchIcon />
        </SearchIconWrapper>
        <StyledInputBase 
          ref={searcInputref}
          onClick={handleOpenSearchMenu}
          // onFocus={onFocus} onBlur={onBlur}
          onChange={onIputSearchChange}
          placeholder="Buscar..."
          inputProps={{ 'aria-label': 'search',}}
        />
      </Search> */}
      <Tooltip title="Barra de búsqueda" >
        <IconButton 
          ref={searcInputref}
          size="large" 
          aria-label="search" 
          color="inherit"
          onClick={handleOpenSearchMenu}
          aria-controls={open ? 'shopping-cart-menu' : undefined}
          aria-haspopup="true"
          aria-expanded={open ? 'true' : undefined}>
          <SearchIcon />
        </IconButton>
      </Tooltip>
      

      <Menu
        anchorEl={searcInputref.current}
        id="search-menu"
        open={open}
        onClose={handleCloseSearchMenu}
        // onClick={handleClose}
        PaperProps={{
            elevation: 0,
            sx: {
                overflow: 'visible',
                filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
                mt: 1.5,
                '& .MuiAvatar-root': {
                    width: 32,
                    height: 32,
                    ml: -0.5,
                    mr: 1,
                },
                '&:before': {
                    content: '""',
                    display: 'block',
                    position: 'absolute',
                    top: 0,
                    right: 14,
                    width: 10,
                    height: 10,
                    bgcolor: 'background.paper',
                    transform: 'translateY(-50%) rotate(45deg)',
                    zIndex: 0,
                },
            },
        }}
        transformOrigin={{ horizontal: 'right', vertical: 'top' }}
        anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
        >
        <Search>
          <SearchIconWrapper>
            <SearchIcon />
          </SearchIconWrapper>
          <StyledInputBase 
            onClick={handleOpenSearchMenu}
            // onFocus={onFocus} onBlur={onBlur}
            onChange={onIputSearchChange}
            placeholder="Buscar..."
            inputProps={{ 'aria-label': 'search',}}
          />
        </Search>

        <Divider/>

        <Box sx={{ width: {xs:'100%', md:'400px'}, maxHeight: '50vh',  overflow: 'hidden', overflowY: "auto", }}>
          
          {foundProducts.length > 0 ?
              foundProducts.map((product, i) =>
                  // <ObjectRow key={i} />
                  <MenuItem sx={{width:'100%'}} key={i}>
                    <SearchedItem 
                      productName={product.name} 
                      productCode={product.codigo}/>
                  </MenuItem>
              )
              :
              <MenuItem>
                  <Typography sx={{ width: '100%', color: 'gray', textAlign:'center' }} component="div" variant="Button">
                      No hay coincidencias de búsqueda
                  </Typography>
              </MenuItem>

            }
        </Box>

      </Menu>

            
    </>
    
  )
}
