const pisoImage = require('../../../assets/images/textures/Carpet.jpg')
// const techoImage = require('../../../assets/images/textures/Concrete.jpg') 


//  piso
const pisoTexture = new THREE.TextureLoader().load(pisoImage);
pisoTexture.wrapS = THREE.RepeatWrapping;
pisoTexture.wrapT = THREE.RepeatWrapping;
pisoTexture.repeat.set( 10, 10 );
const pisoMaterial = new THREE.MeshStandardMaterial({
    roughness: 0.8,
    metalness:0.1,
    map:pisoTexture
});

//  techo
// const techoTexture = new THREE.TextureLoader().load(techoImage);
// techoTexture.wrapS = THREE.RepeatWrapping;
// techoTexture.wrapT = THREE.RepeatWrapping;
// techoTexture.repeat.set( 100, 100 );
const techoMaterial = new THREE.MeshStandardMaterial({
    roughness: 0.8,
    metalness:0.1,
    color: '#846e84'
    // map:techoTexture
});


//  vidrio
const vidrioMaterial = new THREE.MeshStandardMaterial({
    transparent: true,
    opacity: 0.8,
    color: "#d6d6d6", 
    roughness: 0.2,
    metalness:0.1,
});

AFRAME.registerComponent('local-materials', {
    init: function(){
        let el = this.el;

        el.addEventListener("model-loaded", e =>{
            let mesh = el.getObject3D('mesh');

            
            if (!mesh){return;}   
            mesh.traverse(function(node){


                if (node.isMesh){  
                    // console.log('material',node.material.name)

                    switch(node.material.name) {
                        case "Vidrio_Color":
                            node.material = vidrioMaterial;
                            break;
                        case 'Suelo_Color1':
                            node.material = pisoMaterial;
                            break;
                        case 'Techo_Color':
                            node.material = techoMaterial;
                            break;
                    }
                }
            });


        });
    },
  
});