const carrito_texture = new THREE.TextureLoader().load( require('../../../assets/models/carrito/Carrito_Color_PNG.png') );
carrito_texture.flipY = false;

const carrito_material = new THREE.MeshStandardMaterial({
    roughness: 1,
    metalness:0.1,
    opacity: 1,
    alphaTest: 0.01,
    map: carrito_texture,
    transparent: true,
});

// carrito_material.wireframe = true;

AFRAME.registerComponent('shopping-cart-materials', {
  
    init: function() {
        let el = this.el;

        el.addEventListener("model-loaded", e =>{
            let mesh = el.getObject3D('mesh');
            if (!mesh){return;}   
            mesh.traverse(function(node){
                
                if (node.isMesh){
                    // console.log(node.material.name)                            
                    switch(node.material.name) {
                        case "Carro_Color":
                            node.material = carrito_material;
                            // node.material.side = THREE.DoubleSide;
                            break;
                    }
                }
            });
        });
    },
  
});