AFRAME.registerComponent('shopping-cart-events', {
  
    init: function() {
        let el = this.el;
        const sceneEl = document.querySelector('#local-scene');

        document.addEventListener('spawn_product_event', function (e) {
            // console.log('creando producto en el carrito', contenedorProductosRef.current.object3D);
            // console.log('position', contenedorProductosRef.current.object3D.position);
            spawnProduct();
        }, false);
        
        function spawnProduct(){
            const randomColor = Math.random() * 0xffffff;
        
            const producto = document.createElement('a-box');
            const positionCart = el.querySelector('#contenedor-productos-carrito').object3D.getWorldPosition(new THREE.Vector3());
            
            producto.setAttribute('position', {x: positionCart.x , y: positionCart.y + 1.6, z: positionCart.z});
            producto.setAttribute('depth', ".15");
            producto.setAttribute('height', ".15");
            producto.setAttribute('width', ".15");
            producto.setAttribute('ammo-body',{ type: 'dynamic'});
            producto.setAttribute('material', 'color', randomColor);
        
            sceneEl.appendChild(producto);
        
            const Timeout = setTimeout(()=>{
                const elementInCart = sceneEl.querySelector('.entity-cart-product');
        
                if(elementInCart){
                    // console.log(elementInCart.getAttribute('material'))
                    elementInCart.setAttribute('material', 'color', randomColor);
                    elementInCart.setAttribute('visible', 'true');
                    elementInCart.classList.remove('entity-cart-product');
                }
                sceneEl.removeChild(producto);
            }, 500);
        }
    },
  
});