const AFRAME = window.AFRAME;
const THREE = window.THREE;

AFRAME.registerComponent('interactable-product', {
  
    init: function() {
        let el = this.el;

        //Código aquí********************************************************************
        el.addEventListener("click", function(){ 
            const ClickProductEvent = new CustomEvent('open_product_modal', { 
                detail:{
                  product_code: '2322' //'2322' con descuento //'3528' con descri // 6130 sin stock
                } 
            });
            document.dispatchEvent(ClickProductEvent);
        });
        //Código aquí********************************************************************

    },
  
  });