require('./components/aframe-components/follow-entity');
require('./components/aframe-components/load-glb-collision');
require('./components/aframe-components/Player/movement-controls');
require('./components/aframe-components/Personalization/character-textures');
require('./components/aframe-components/Player/load-character-prefs');
// require('./components/aframe-components/ExperienceComponents/mall-materials');
require('./components/aframe-components/Local/local-materials');
// require('./components/aframe-components/ExperienceComponents/local-teleporter');
require('./components/aframe-components/Player/naf-remote-avatar');
require('../modules/aframe-orbit-controls-component');
require('./components/aframe-components/aframe-look-at-component.min');
require('./components/aframe-components/Local/shopping-cart-materials');
require('./components/aframe-components/Local/interactable-product');
require('./components/aframe-components/Local/shopping-cart-events');
require('./components/aframe-components/adjust-image');
require('./components/aframe-components/Local/lod-shelf');
require('./components/aframe-components/Local/instantiate-shelves');


//  Importación de componentes de React
import BottomMenu from "./components/react-components/experience/BottomMenu/BottomMenu";
import ForwardButton from "./components/react-components/Buttons/ForwardButton";
import ProductModal from "./components/react-components/local/ProductModal";
import Header from "./components/react-components/Header/Header";
import ShoppingCart from "./components/react-components/ShoppingCart/ShoppingCart";
import SearchInput from "./components/react-components/SearchInput/SearchInput";
import Joystick from "./components/react-components/Joystick/Joystick";

//  Importación de contextos
import { UserDataContext, emptypreferenciasUsuario } from "./context/UserDataContext";
import { MultiplayerProvider } from "./context/MultiplayerContext";
import { UserDataProvider } from "./context/UserDataContext";
import { ContextTemplateProvider, ContextTemplate } from "./components/react-components/ContextTemplate";

// Importar Menu
// import CarritoCompras from "./components/react-components/CarritoCompras/CarritoCompras"

//  Importo la libreria de axios
const axios = require('../modules/axios.min').default; 

//  Importo plantillas de datos para los parametros de consulta de los diferentes tipos de gondolas
import { gondolas_1_aws_parameters,
        gondolas_2_aws_parameters,
        gondolas_3_aws_parameters} from "./components/aframe-components/Local/localsData";  

const Local = () => {

  // Contexto de los datos de usuario
  const { userData } = useContext(UserDataContext);

  //  Estados
  // const [ idLocal, setIdLocal ] = useState("beea88cd-ff6e-4c47-9035-8a85f41b13f3");
  const [ EventVRToken, setEventVRToken ] = useState('');

  useEffect(() => {
    //  Verifico que no sea un estado sin datos aún consultados del locastorage en userdataconstext
    if (userData !== emptypreferenciasUsuario) {

      //  verifico que existan datos de personalización del usuario
      if (userData?.preferenciasUsuario?.avatar != '' &&
        userData?.preferenciasUsuario?.ropa != {}) {
        // console.log('userData verificada', userData)
        //  Obtengo la entidad del modelo del jugador y le easigno el componente para aplicar modelo y ropa
        const playerEntity = document.querySelector('#player-model');
        playerEntity.setAttribute('load-character-prefs',
          { userPrefs: JSON.stringify(userData.preferenciasUsuario) });

        const nameTextEntity = document.querySelector('#player-text-name');
        if (nameTextEntity) {
          nameTextEntity.setAttribute('text', { value: userData.preferenciasUsuario.nombre || 'SIN NOMBRE' });
        }

      }
    }

  }, [userData]);

  //Función para detectar presión de una tecla y activar el movimiento del player
  const handleInteraction = (e)=>{
    document.activeElement.blur()
    document.querySelector('a-scene').sceneEl.canvas.focus();

    document.removeEventListener('keydown', handleInteraction);
  }
  //  Al iniciar la aplicación 
  useEffect(()=>{
    // Obtengo el id del local drectamente de la URL
    var url = new URL(window.location.href);
    var url_id_local = url.searchParams.get("ID");

    if(url_id_local && url_id_local !== ''){
      //Obtengo los datos de ese local
      getDataEventVr(url_id_local);
    }

    

    document.addEventListener('keydown', handleInteraction);
    
  },[]);

  const getDataEventVr = ( idLocal ) => {
    const bodyLogin = {
      "request_data": {
      "type_action": "login",
          "data": {
              "correo_usuario": process.env.EVENTVR_USER,
              "clave_usuario": process.env.EVENTVR_PASSWORD
          }
      },
      "headers": {
          "Content-Type": "application/json",
          "Accept": "application/json"
      }
    }

    // Axios
    axios.post(process.env.URL_AMAZON_ENDPOINTS + 'login', bodyLogin, {
        headers: {
            "Content-Type": "application/json",
            "Accept": "application/json",
        },
    })
    .then((response) => {
        if(response.status == '200' && response.data.status != 'failed'){
            setEventVRToken (response.data.result.token);
            // console.log('token', response.data.result.token);

            //Obtengo los datos de los locales para obtener el tipo de local y el resto de información que necesio
            const responseLocales = getLocalData(response.data.result.token, process.env.EVENTVR_ID_EMPRESA_USUARIO, idLocal);
            responseLocales.then(async(response_local) => {

              if(response_local.status == '200' && response_local.data.status != 'failed'){

                const localInfo = response_local.data.body.data[0];

                if(localInfo){

                  // Reubico al jugador según el tipo de local
                  const playerEntity = document.querySelector('#player-entity');
                  if(playerEntity){

                    const ammo_boddy_component = playerEntity.components["ammo-body"];
                    // console.log('body', ammo_boddy_component)

                    // apply the flag
                    // ammo_boddy_component.body.setCollisionFlags(2);
                    // ammo_boddy_component.pause();
                    
                    let new_player_position = {x:0, y:0.3, z:0};

                    switch(localInfo.tipo_distribucion_local){
                      case '1':
                        new_player_position = {x:0, y:0.1, z:0};
                        break;
                      case '2':
                        new_player_position = {x:6.454, y:0.1, z:-7.960};
                        break;
                      case '3':
                        new_player_position = {x:6.517, y:0.1, z:-7.921};
                        break;
                    }

                    // playerEntity.setAttribute('position', new_player_position);
                    playerEntity.object3D.position.set(
                      new_player_position.x,
                      new_player_position.y,
                      new_player_position.z
                    );

                    // change the collision flag to the KINEMATIC_BODY
                    ammo_boddy_component.body.setCollisionFlags(2);

                    // sync the physisc transforms to the THREEJS transform
                    ammo_boddy_component.syncToPhysics();

                    // Se debe poner un timeout para que se aplique correctamente
                    // referencia: https://stackoverflow.com/a/66425203
                    setTimeout(() => {
                      // restore the original collision flags
                      ammo_boddy_component.updateCollisionFlags();

                      // reset the speed, or the body will bounce away
                      ammo_boddy_component.body.setLinearVelocity(new Ammo.btVector3(0, 0, 0));

                    }, 10);
                    
                  }

                  //  Obtengo la entidad del modelo del local para cambiar su modelo
                  const modelLocalEntity = document.querySelector('#local-model');
                  if(modelLocalEntity){

                    // Asigno el modelo del local según el tipo de distribución que tenga el local
                    modelLocalEntity.setAttribute('load-glb-collision',{
                      model:'#model-asset-local-'+ localInfo.tipo_distribucion_local,
                      body:'static',
                      shape: 'mesh' ,
                      scale: '50 50 50'
                    })

                  }

                }
                
                //Obtener góndolas tipo ***************************************
                getShelfsEventVR(gondolas_1_aws_parameters, response.data.result.token, idLocal);
                getShelfsEventVR(gondolas_2_aws_parameters, response.data.result.token, idLocal);
                getShelfsEventVR(gondolas_3_aws_parameters, response.data.result.token, idLocal);
                //Obtener góndolas tipo ***************************************
              }
            })
            .catch(error => {
                console.error('error : ', error)
            });

            

        }
    })
    .catch(error => {
        console.error('error : ', error)
    });
  }



  //  Consulto los locales para obtener la información del local en que ingresé
  const getLocalData = (token, idEmpresaUsuario, id_local) =>{
    const body ={
      "request_data": {
        "type_action": "get_one",
        "data": {
          "id_local": id_local,
          "id_empresa_usuario_local": idEmpresaUsuario
        }
      },
      "headers": {
        "Content-Type": "application/json",
        "Accept": "application/json",
        "Authorization": token
      }
    }

    // Axios
    return axios.post(process.env.URL_AMAZON_ENDPOINTS + 'locales', body, {
      headers: {
          "Content-Type": "application/json",
          "Accept": "application/json",
      },
    })
    

  }


  // Consulto los datos de un tipo de góndola
  // Se realiza para cada tipo de góndola presente en el local
  const getShelfsEventVR = async(aws_parameters, token, idLocal) => {
    
    try {
      const responseGondolas = await axios.post(process.env.URL_AMAZON_ENDPOINTS + 'gondolas', {
          "request_data": {
              "type_action": aws_parameters.type_action_get_gondolas,
              "data": {
                  [aws_parameters.id_local_get_gondolas_label]: idLocal
              }
          },
          "headers": {
              "Content-Type": "application/json",
              "Accept": "application/json",
              "Authorization": token
          }
      });

      if(responseGondolas.status == '200' && 
        responseGondolas.data && 
        responseGondolas.data.status != 'failed'){
          
          const gondolasArray = responseGondolas.data.body.data;
          
          const container_gondolas_1 = document.querySelector(aws_parameters.id_container_3D);
          if(container_gondolas_1){
              container_gondolas_1.setAttribute('load-shelves',{
                shelvesDataArray: JSON.stringify(gondolasArray),
                awsParameters: JSON.stringify({...aws_parameters, token: token})
              })

              container_gondolas_1.setAttribute('gltf-model',aws_parameters.shelf_asset_model);
          }
      }
    }
    catch (err) {
        console.error(err);
    }
  }

  return (
    <>
      <Header>
        {/* <CarritoCompras /> */}
        <ShoppingCart/>
        <SearchInput/>
      </Header>
    
      <ProductModal />
      <BottomMenu />
      
      {/* Verifico si es un dispositivo móvil y grafico el botón de movimiento táctil */}
      {/* {AFRAME.utils.device.isMobile() && <ForwardButton />} */}
      <Joystick/>
    </>

  );
}

//  Importo la distribución inicial de estantes en el local
// import LocalDistribution1 from "./components/react-components/local/LocalDistribution1";

// const Local3D = () => {

//   return (
//     <LocalDistribution1/>
//   );
// }


/*  El componente principal de React se debe cargar una vez el documento y los script han cargado
    Esto se puede cambiar en WEBPACK
    new HtmlWebpackPlugin({...scriptLoading: 'blocking',}),
    Si estuviese scriptLoading: 'defer' esto no es necesario pero Aframe 1.3.0 requiere 'blocking'.
    'blocking' carga los chunks (scripts) antes de que se cargue el documento, 
    así se reconocen las etiquetas AFRAME
*/
document.addEventListener('DOMContentLoaded', activate);

function activate() {

  // console.log('document.getElementById("root")',document.getElementById("root"))
  const root = ReactDOM.createRoot(document.getElementById("root"));
  root.render(
    // <React.StrictMode>
      <ContextTemplateProvider>
        <UserDataProvider>
          <MultiplayerProvider>
            <Local />
          </MultiplayerProvider>
        </UserDataProvider>
        
      </ContextTemplateProvider>

    // </React.StrictMode>
  );

  // const root3D = ReactDOM.createRoot(document.getElementById("shelfs-3D-container"));
  // root3D.render(
  //   // <React.StrictMode>
  //     <Local3D />
  //   // </React.StrictMode>
  // );
};


