AFRAME.registerComponent('lod-shelf', {

    schema: {
        selectorToHide:       {type: 'string', default: '#products-container'},
    },

    init: function(){
        let el = this.el;
        
        const elToHide = el.parentNode.querySelector(this.data.selectorToHide);
        elToHide.setAttribute('visible', false);
        
        // al detectar colisión se activan los productos
        el.addEventListener('collidestart', (e) => {
            //  Verifico que la colisión es con un jugador para activar la visibilidad
            if(e.detail.targetEl.id==='player-entity'){
                elToHide.setAttribute('visible', true);
            }
        })

        // al detectar salida de la colisión se desactivan los productos
        el.addEventListener('collideend', (e) => {
            //  Verifico que la colisión es con un jugador para desactivar la visibilidad
            if(e.detail.targetEl.id==='player-entity'){
                elToHide.setAttribute('visible', false);
            }
            
        })
        
    }
});