import React, { useContext, useState } from 'react';
import { useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import DeleteIcon from '@mui/icons-material/Delete';
import Grid from '@mui/material/Grid';
import Input from '@mui/material/Input';
import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';

//  Contexto de las notificaciones
import { ContextTemplate } from '../ContextTemplate';
import { UserDataContext } from '../../../context/UserDataContext';

export default function ProductCart({ name, itemCode, itemId, image, quantity, price, onRemoveProductCart }) {
  const theme = useTheme();
  const { openNotification } = useContext(ContextTemplate); // contexto del tema general y notificacion
  const { hoyosData } = useContext(UserDataContext); // Contexto de los datos de usuario
  const { token, typeDelivery} = hoyosData; // Desestructuro los datos de hoyostools
  
  const [quantityInput, setQuantityInput] = useState(quantity);
  
  const handleQuantitychange = (e) => {
    // setQuantityInput(e.target.value);
    if(e.target.value >= 1){
      updateItemQuantity(e.target.value);
    }
    
  }

  const actionDelete =(
    <Button color="secondary" size="small" onClick={()=>removeProduct()}>
      ELIMINAR
    </Button>
  );

  const onRemoveProductClick = () => {
    openNotification('¿Desea eliminar del carrito el producto ' + name + '?', 
      'warning', actionDelete);
  }

  const removeProduct = () => {
    //console.log('eliminando', process.env.ENDPOINT_REMOVE_DATA_BASKET)
    let formData = new FormData();
    formData.append("itemCode", itemCode);
    formData.append("tipoEntrega", typeDelivery);

    fetch(process.env.ENDPOINT_REMOVE_DATA_BASKET, {
      method: 'POST',
      body: formData,
      headers: {
        'Authorization': 'Bearer ' + token
      }
    })
      .then(res => res.json())
      .then(response => {
        console.log('Producto Eliminado: ', response)
        openNotification("Producto eliminado correctamente", 'success');
        onRemoveProductCart()
      })
      .catch(error => openNotification('No se ha podido eliminar este producto','error'));
  }


  function updateItemQuantity(value) {
    
    const url = process.env.ENDPOINT_UPDATE_ITEM_QUANTITY;
  
    let formData = new FormData();
  
    formData.append("productId", itemId);
    formData.append("productQuantity", value);
    formData.append("tipoEntrega", typeDelivery);
  
    fetch(url, {
      method: 'POST',
      body: formData,
      headers: {
        'Authorization': 'Bearer ' + token
      }
    })
      .then(res => res.json())
      .then(response => {
        //console.log('Success:', response)
        if(!response.error){
          onRemoveProductCart();
          setQuantityInput(value);
        }else{
          openNotification(response.message,'error')
        }
      })
      .catch(error => {
        console.error('Error:', error)
        openNotification('No se ha podido modificar la cantidad de este producto','error')
      });
  }

  return (
    <Card sx={{ display: 'flex' }}>
      <Box sx={{ display: 'flex', flexDirection: 'column' }}>
        <CardContent sx={{ flex: '1 0 auto' }}>
          <Box sx={{ display: 'flex', alignItems: 'center', position:'absolute', top:'1rem', right:'2rem' }}>
              {/* <Typography>Filled</Typography> */}
              <DeleteIcon fontSize="small" onClick={onRemoveProductClick}/>
          </Box>
          <Grid container wrap="nowrap" spacing={1}>
            <Grid item>
              <CardMedia
                component="img"
                sx={{ width: '3rem', height:'3rem', mr:2 }}
                image={image}
              />
                {/* <img src="https://cdn-icons-png.flaticon.com/512/107/107831.png" width={80} height={80} /> */}
            </Grid>
            <Grid item>
                <Typography noWrap sx={{ maxWidth: '20ch' }}>{ name }</Typography>
                <Stack spacing={2} direction="row">
                    <Stack>
                        <Input 
                          type="number" 
                          sx={{ width: 40, height: 30 }} 
                          value={quantityInput}
                          // defaultValue={quantity}
                          onChange={handleQuantitychange}
                          ></Input>
                    </Stack>
                    <Typography variant="caption">X</Typography>
                    <Typography color="textSecondary">{price}</Typography>
                </Stack>
            </Grid>
          </Grid>
        </CardContent>
        
      </Box>
      
      {/* <CardMedia
        component="img"
        sx={{ width: 151 }}
        image={<CategoryIcon sx={{ m:2, fontSize:'3rem', color: 'gray' }}/>}
      /> */}
    </Card>
  );
}